import React from 'react'
import PropTypes from 'prop-types'

export default function Navbar(props) {
  
  return (
    <nav className={`navbar navbar-expand-lg bg-${props.mode} navbar-${props.mode} text-light"`}>
  <div className="container-fluid text-light">
    <a className="navbar-brand" href="/">{props.title}</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <a className="nav-link active" aria-current="page" href="/">{props.Home}</a>
        </li>
        
        
        
        {/* <li className="nav-item">
          <a className="nav-link disabled" href="/">Disabled</a>
        </li> */}
      </ul>
              <div className={`form-check form-switch text-${props.mode==='dark'?'light':'dark'}`}>
          <input type="checkbox" className ="form-check-input" id="flexSwitchCheckDefault" onClick={props.enableDark}/>
          <label className="form-check-label" onClick={props.change} htmlFor="flexSwitchCheckDefault">{props.Text}</label>
          
        </div>
        
    </div>
  </div>
</nav>
  )
}
 Navbar.propTypes = {
    Home: PropTypes.string
 }