

import { useState } from "react";

// import Accordian from "./components/Accordian";
import Textform from "./components/Textform";
import Navbar from "./components/Navbar";
import Alert from "./components/Alert";
import React  from 'react';
// import { BrowserRouter as Router, Route, Routes} from 'react-router-dom';


// import Textform from "./components/Textform";  

function App() {
 
 

  const [mode, setmode] = useState("light");
  const [alert, setalert] = useState("null");
  
  
  const showAlert = (message, type)=>{
    setalert({
        message:message,
        type:type
    })
  };
  setTimeout(() => {
    showAlert(null);
  }, 3000);
  

  const enableDark = ()=> {
    if(mode === 'light')
    {
      setmode("dark");
      document.body.style.backgroundColor = '#363636';
  

      showAlert("you are Successfully Switched to dark mode", "success");
    }
    else{
      setmode("light");
      document.body.style.backgroundColor = 'white';
      showAlert("you are Successfully Switched to Light mode", "success");
    }
  };
 
   
  
  return (
   <>
  
   
<Navbar title = "TextAnalysis" Home = "Home" mode = {mode} enableDark = {enableDark} Text = "Go Dark"  />
<Alert alert = {alert}/>
<Textform heading = "Enter The Text to Capatalize !" mode={mode} showAlert = {showAlert}/>
 

   </>
   
  );
}

export default App;
