import React, {useState} from 'react'


export default function Textform(props) {
    const handleclick = ()=>{
        const newtext = text.toUpperCase();
        setText(newtext);
        props.showAlert("Uppercase Successfull!", "success");
    }
    const handleloclick = ()=>{
        const newtext = text.toLowerCase();
        setText(newtext);
        props.showAlert("Lowercase Successfull!", "success");
    }
    const handleOnchange = (event)=>{
        setText(event.target.value);

    }
    const clear = (event)=>{
        setText(" ");
        props.showAlert(" text cleared!", "success");

    }
    const copy = ()=>{
        var text = document.getElementById("exampleFormControlTextarea1");
        text.select();
        navigator.clipboard.writeText(text.value);
        props.showAlert("Text Copied", "success");
    }

        const[text, setText] = useState(" ");
        
  return (
    <>
    <div className='container'>
        <h1 style={{color:props.mode==='light'?'black':'white'}}>{props.heading}</h1>
       
<div className="mb-3">
  
  <textarea className="form-control" id="exampleFormControlTextarea1" onChange = {handleOnchange} value={text} rows="8" style={{color:props.mode==='light'?'black':'white',backgroundColor:props.mode==='light'?'white':'grey', border:props.mode==='dark'?'2px solid white':'2px solid black'}}></textarea>
</div>
    <button className = "btn btn-primary" onClick = {handleclick}> Uppercase </button>
    <button className = "btn btn-success mx-3" onClick = {handleloclick}> Lowercase The Text </button>
    <button className = "btn btn-dark mx-1" onClick = {clear}> Clear </button>
    <button className = "btn btn-primary mx-1" onClick = {copy}> Copy Text </button>
        
    </div>
    <div className="container" style={{color:props.mode==='light'?'black':'white'}}> 
        <h3 className='my-3'> Words Counter</h3>
        
        <p> you've wrote {text.split(" ").length} words and {text.length} character</p>
        <h2>Preview</h2>
        <p> {text.length>0?text:"please Write something to see the preview"}</p>
    </div>
     
    </>
  )
}
